}
.foot-link {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: #fff;
  text-align: center;
  padding: 10px;
  border-radius: 30px;
  color: #c6a325;
  margin-right: 10px;
}
.foot-link:hover,
.foot-link:focus {
  background-color: #c6a325;
  color: #fff;
}
.copy {
